export default {
	props: {
		// itemuv-tabbarvalue
		name: {
			type: [String, Number, null],
			default: null
		},
		// uv-ui
		icon: {
			icon: String,
			default: ''
		},
		// uv-tabbariconSize=20
		iconSize: {
			type: [String, Number],
			default: ''
		},
		// 
		badge: {
			type: [String, Number, null],
			default: null
		},
		// badge
		dot: {
			type: Boolean,
			default: false
		},
		// 
		text: {
			type: String,
			default: ''
		},
		// topright
		badgeStyle: {
			type: [Object, String],
			default: 'top: 6px;right:2px;'
		},
		...uni.$uv?.props?.tabbarItem
	}
}