export default {
	props: {
		// 12
		span: {
			type: [String, Number],
			default: 12
		},
		// (12)
		offset: {
			type: [String, Number],
			default: 0
		},
		// `start`(`flex-start`)`end`(`flex-end`)`center``around`(`space-around`)`between`(`space-between`)
		justify: {
			type: String,
			default: 'start'
		},
		// topcenterbottomstretch
		align: {
			type: String,
			default: 'stretch'
		},
		// 
		textAlign: {
			type: String,
			default: 'left'
		},
		...uni.$uv?.props?.col
	}
}