import http from "@/util/request.js";

// 
export function getDevicePage(params) {
	return http.request({
		url: 'bind/getDevice',
		method: 'get',
		params,
	})
}

// 
export function getDeviceData(params) {
	return http.request({
		url: 'bind/getDeviceData',
		method: 'get',
		params,
	})
}

