export default {
	props: {
		// name
		value: {
			type: [String, Number, null],
			default: null
		},
		// iPhoneX
		safeAreaInsetBottom: {
			type: Boolean,
			default: true
		},
		// 
		border: {
			type: Boolean,
			default: true
		},
		// z-index
		zIndex: {
			type: [String, Number],
			default: 9
		},
		// 
		activeColor: {
			type: String,
			default: '#1989fa'
		},
		// 
		inactiveColor: {
			type: String,
			default: '#7d7e80'
		},
		// 
		fixed: {
			type: Boolean,
			default: true
		},
		// fixed
		placeholder: {
			type: Boolean,
			default: true
		},
		// 
		iconSize: {
			type: [String, Number],
			default: 20
		},
		...uni.$uv?.props?.tabbar
	}
}