export default {
	props: {
		// 
		seconds: {
			type: [String, Number],
			default: 60
		},
		// 
		startText: {
			type: String,
			default: ''
		},
		// 
		changeText: {
			type: String,
			default: 'X'
		},
		// 
		endText: {
			type: String,
			default: ''
		},
		// H5
		keepRunning: {
			type: Boolean,
			default: false
		},
		// 
		uniqueKey: {
			type: String,
			default: ''
		},
		...uni.$uv?.props?.code
	}
}