export default {
	props: {
		// col
		gutter: {
			type: [String, Number],
			default: 0
		},
		// `start`(`flex-start`)`end`(`flex-end`)`center``around`(`space-around`)`between`(`space-between`)
		justify: {
			type: String,
			default: 'start'
		},
		// topcenterbottom
		align: {
			type: String,
			default: 'center'
		},
		...uni.$uv?.props?.row
	}
}