export default {
	props: {
		// 
		text: {
			type: [Array],
			default: ''
		},
		// 
		icon: {
			type: [String, Boolean, null],
			default: 'volume'
		},
		// link-closable-
		mode: {
			type: String,
			default: ''
		},
		// 
		color: {
			type: String,
			default: '#f9ae3d'
		},
		// 
		bgColor: {
			type: String,
			default: '#fdf6ec'
		},
		// px
		fontSize: {
			type: [String, Number],
			default: 14
		},
		// px(px)
		speed: {
			type: [String, Number],
			default: 80
		},
		// direction = row
		step: {
			type: Boolean,
			default: false
		},
		// ms
		duration: {
			type: [String, Number],
			default: 1500
		},
		// `direction="column"`
		// HX2.6.11App 2.5.5+H5 2.5.5+
		disableTouch: {
			type: Boolean,
			default: true
		},
		// `direction="column"`
		disableScroll: {
			type: Boolean,
			default: false
		},
		...uni.$uv?.props?.columnNotice
	}
}